import './static/css/index.less';
import $ from 'jquery';
import options from './data';
import demandData from './mock/demandData';

let instance;

$('#menu a').click(e => {
    let path = $(e.target).attr('path');
    instance && instance.dispose();

    require.ensure([], function () {
        $('#main').html('');
        let Chart = require('./' + path + '.js').default;
        instance = new Chart(options[Chart.type()]);

        if (Chart.type() === 'Demand') {
            instance.on('loadData', function () {
                let rand = Math.floor(Math.random() * demandData.length);
                instance.setData && instance.setData(demandData[rand]);
            });
        }
    });
});

$('#menu a:last').click();



